.imageSlider__parentContainer{
  overflow: hidden;
  display: flex;
  position: relative;
}
.imageSlider__btnContainer {
  display: flex;
  position: absolute;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  align-self: center;

}
.imageSlider__btn{
  padding: 10px;
  font-size: 75px;
  color: white;
  -webkit-box-flex: 1;
  flex: 1;
}
.imageSlider__image {
  -webkit-transition-duration:2s;
  transform:translateX(0px);
}
.imageSlider__nextBtn{
  text-align: right;
}
.imageSlider__previousBtn{
  text-align: left;
}